.wrapper_pager{
      width: 46rem;
      height: 56rem;
      background: var(--bg-100);
      padding: 1.5rem;
      border-radius: 1.5rem;
      position: absolute;
      top: 1.2rem;
      left: calc(50% - 23rem);
      opacity: 0;
      visibility: hidden;
      transform: translateX(-100%);
      transition: .5s ease-in-out;
}

.pager_content{
      width: 100%;
      height: 46rem;
      background: var(--bg-200);
      border-radius: 1rem;
      padding: 2rem;
      margin-top: 2rem;
}

.home.active{
      opacity: 1;
      visibility: visible;
      transform: translateX(0);
}

.photos.active{
      opacity: 1;
      visibility: visible;
      transform: translateX(0);
}

.videos.active{
      opacity: 1;
      visibility: visible;
      transform: translateX(0);
}

.relation.active{
      opacity: 1;
      visibility: visible;
      transform: translateX(0);
}