.relation_header{
      width: 100%;
      height: auto;
      gap: 1rem;
}

.relation_avatar{
      width: 16rem;
      height: 16rem;
      flex-shrink: 0;
      border-radius: 1rem;
      overflow: hidden;
}

.relation_info{
      flex-grow: 1;
      flex-direction: column;
}

.relation_info h2{
      font-size: 2.4rem;
}

.relation_info strong{
      font-size: 1.8rem;
      font-weight: 500;
}

.relation_info span{
      text-transform: capitalize;
      margin-top: .5rem;
      color: var(--text-200);
}

.relation_nav{
      margin-top: 1rem;
      width: 100%;
      align-items: center;
      justify-content: space-between;
}

.relation_nav a{
      width: 10rem;
      height: 2.8rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--primary);
      border-radius: 999px;
      color: var(--text-100);
      text-transform: uppercase;
      font-size: 1.2rem;
}

.relation_description{
      margin-top: 2rem;
      padding-top: 1.5rem;
      position: relative;
}

.relation_description:before{
      width: 100%;
      height: .2rem;
      background: var(--v-gradient);
      top: 0;
      left: 0;
}

.relation_description p{
      white-space: pre-line;
      color: var(--text-200);
}