.header_pager{
      width: 100%;
      align-items: center;
      justify-content: space-between;
      gap: 1rem;
}

.header_user{
      width: calc(100% - 4rem);
      gap: .5rem;
}

.header_user__avatar{
      width: 4.4rem;
      height: 4.4rem;
      border: solid .2rem var(--primary);
      border-radius: 50%;
      align-items: center;
      justify-content: center;
}

.header_user__img{
      width: 3.6rem;
      height: 3.6rem;
      border-radius: 50%;
      overflow: hidden;
}

.header_user__info{
      padding-top: .25rem;
}

.header_user__info span{
      text-transform: uppercase;
      font-size: 1.2rem;
}

.header_user__info h2{
      font-weight: 500;
      text-transform: capitalize;
}

.header_dots{
      width: .6rem;
      height: .6rem;
      background: var(--text-300);
      border-radius: 50%;
      box-shadow: -1rem 0 var(--text-300), -2rem 0 var(--text-300);
}