.friends_content{
      flex-shrink: 0;
      width: 17rem;
      height: auto;
}

.friends_header{
      width: 100%;
      height: 4.2rem;
      position: relative;
      align-items: center;
      justify-content: center;
}

.friends_header:before,
.friends_header:after{
      width: 100%;
      height: .2rem;
      background: var(--v-gradient);
      left: 0;
}

.friends_header:before{
      top: 0;
}

.friends_header:after{
      bottom: 0;
}

.friends_header h3{
      text-transform: uppercase;
      font-weight: 500;
      font-size: 1.4rem;
}

.friends_list{
      width: 13rem;
      height: auto;
      margin-top: 10rem;
      margin-left: 2rem;
      position: relative;
}

.friend_row{
      width: 13rem;
      height: 13rem;
      padding: 1rem;
      position: relative;
}

.friend_row:before,
.friend_row:after{
      width: .2rem;
      height: 11rem;
      background: var(--h-gradient);
      top: 1rem;
}

.friend_row:before{
      left: 0;
}

.friend_row:after{
      right: 0;
}

.friend_box{
      position: relative;
      width: 11rem;
      height: 11rem;
      overflow: hidden;
      border-radius: 1rem;
}

.friend_box:before{
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,.75);
      z-index: 2;
      opacity: 0;
      transition: .3s ease-in-out;
}

.friend_row:hover .friend_box:before{
      opacity: 1;
}

.friend_box img{

}

.friend_box a{
      position: absolute;
      width: 100%;
      height: 11rem;
      left: 0;
      top: calc(50% - 5.5rem);
      text-align: center;
      line-height: 11rem;
      color: var(--text-100);
      font-weight: 500;
      transform: translateY(100%);
      transition: .3s ease-in-out;
      z-index: 3;
      text-transform: uppercase;
      font-size: 1.2rem;
}

.friend_row:hover .friend_box a{
      transform: translateY(0);
}

button.slick-arrow{
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      z-index: 2;
      left: calc(50% - 2rem);
}

button.slick-arrow.slick-next{
      top: -6rem;
      transform: rotate(-90deg)
}

button.slick-arrow.slick-prev{
      top: calc(100% + 2rem);
      transform: rotate(-90deg);
}

button.slick-arrow.slick-next:before,
button.slick-arrow.slick-prev:before{
      width: 1.4rem;
      height: 1.4rem;
      color: transparent;
      transform: rotate(45deg);
      top: 1.2rem;
      left: .8rem;
}

button.slick-arrow.slick-next:before{
      border-top: solid .3rem var(--primary);
      border-right: solid .3rem var(--primary);
}


button.slick-arrow.slick-prev:before{
      border-bottom: solid .3rem var(--primary);
      border-left: solid .3rem var(--primary);
      left: 1.4rem;
}