.header_user__pager{
      width: 100%;
      height: auto;
      gap: 2rem;
}

.user_pager__avatar{
      width: 16rem;
      height: 16rem;
      flex-shrink: 0;
      border-radius: 1rem;
      overflow: hidden;
}

.user_pager__info{
      flex-grow: 1;
}

.user_location{
      align-items: first baseline;
      gap: .5rem;
}

.user_location img{
      width: 3.5rem;
}

.user_location strong{
      font-size: 1.6rem;
      font-weight: 500;
}

.user_birthday{
      align-items: center;
      gap: 1rem;
      margin-top: .5rem;
}

.user_birthday *{
      font-size: 2.5rem;
      color: var(--text-200);
}

.user_birthday span{
      font-size: 1.6rem;
      color: var(--text-100);
}

.user_link{
      margin-top: 1rem;
      align-items: center;
      justify-content: space-between;
}

.user_link a{
      width: 10rem;
      height: 2.8rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--primary);
      color: var(--text-100);
      border-radius: 999px;
      text-transform: uppercase;
      font-size: 1.2rem;
}

.user_social{
      margin-top: 1rem;
}

.social_menu{
      align-items: center;
      gap: 1rem;
}

.social_menu a{
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--text-100);
}

.social_menu a *{
      font-size: 2rem;
}

.btn_social.facebook{
      background: var(--facebook);
}

.btn_social.twitter{
      background: var(--twitter);
}

.btn_social.instagram{
      background: var(--instagram);
}

.btn_social.tiktok{
      background: var(--tiktok);
}

.article_user__description{
      margin-top: 2rem;
      padding-top: 2rem;
      position: relative;
}

.article_user__description:before{
      width: 100%;
      height: .2rem;
      background: var(--v-gradient);
      top: 0;
      left: 0;
}

.article_user__description p{
      color: var(--text-200);
      white-space: pre-line;
}