.videos_content{
      width: 100%;
      height: 42.5rem;
      padding-right: .5rem;
      overflow-y: auto;
}

.videos_list{
      width: 100%;
      height: auto;
}

.video_row{
      width: 100%;
      height: auto;
      position: relative;
      padding-bottom: 2rem;
      margin-bottom: 2rem;
}

.video_row:before{
      width: 100%;
      height: .2rem;
      background: var(--v-gradient);
      bottom: 0;
      left: 0;
}

.video_box{
      width: 100%;
      height: 22rem;
      border-radius: 1rem;
      overflow: hidden;
}

.video_box iframe{
      border: none;
      outline: none;
}

.video_row p{
      width: 100%;
      height: auto;
      margin-top: 1rem;
      text-align: center;
      text-transform: capitalize;
}