.background{
      position: fixed;
      width: 100%;
      height: 100vh;
      top: 0;
      left: 0;
      background: var(--primary);
      overflow: hidden;
}

.background:before{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.9);
      z-index: 1;
}

.background img{
      filter: grayscale(1);
}