.aside_content{
      width: 35rem;
      height: 100%;
      background-image: url(../assets/img/aside_bg.svg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: top right;
      position: fixed;
      top: 0;
      left: 0;
      padding-top: 13.2rem;
      z-index: 10;
}

.user_wrapper{
      width: 100%;
      flex-direction: column;
      align-items: center;
      padding: 0 4rem;
}

.user_avatar{
      width: 13rem;
      height: 13rem;
      align-items: center;
      justify-content: center;
      position: relative;
}

.user_avatar:before{
      width: 100%;
      height: 100%;
      border: solid .3rem var(--light);
      border-right: solid .3rem transparent;
      border-radius: 50%;
      top: 0;
      left: 0;
      transform: rotate(-45deg);
      box-sizing: border-box;
}

.user_avatar__img{
      width: 11rem;
      height: 11rem;
      border-radius: 50%;
      overflow: hidden;
}

.user_infor{
      flex-direction: column;
      align-items: center;
      color: var(--text-100);
      margin-top: 2rem;
}

.user_infor h1,
.user_infor strong{
      font-size: 1.8rem;
      text-transform: uppercase;
      font-weight: 500;
}

.user_infor strong{
      margin-top: 1rem;
}

.user_infor span{
      text-transform: capitalize;
}

.user_infor a{
      display: flex;
      margin-top: 1rem;
      align-items: center;
      background: var(--bg-200);
      height: 2.8rem;
      padding: 0 3rem;
      border-radius: 999px;
      text-transform: uppercase;
      font-size: 1.2rem;
      color: var(--text-100);
}

.navbar_menu{
      padding-left: 2rem;
      margin-top: 5rem;
}

.menu_wrapper{
      width: 24rem;
      flex-direction: column;
      gap: 1rem;
}

.menu_link{
      width: 24rem;
      height: 4rem;
      align-items: center;
      gap: 1rem;
      justify-content: flex-end;
      padding-right: 4rem;
      position: relative;
      transition: .3s ease-in-out;
      background: none;
}

.menu_link:before,
.menu_link:after{
      width: 20rem;
      height: .2rem;
      background: var(--v-gradient);
      transition: .5s ease-in-out;
      opacity: 0;
}

.menu_link:before{
      top: 0;
      right: 0;
}

.menu_link:after{
      bottom: 0;
      left: 0;
}

.menu_link:hover:before,
.menu_link:hover:after,
.menu_link.active:before,
.menu_link.active:after{
      opacity: 1;
}

.menu_link:hover:before,
.menu_link.active:before{
      transform: translateX(-4rem);
}

.menu_link:hover:after,
.menu_link.active:after{
      transform: translateX(4rem);
}

.menu_link *{
      color: var(--bg-200);
      font-size: 2rem;
      transition: .2s ease-in-out;
}

.menu_link span{
      text-transform: uppercase;
      font-size: 1.4rem;
      color: var(--text-100);
}

.menu_link:hover *,
.menu_link.active *{
      color: var(--text-100);
}

.menu_link:hover,
.menu_link.active{
      padding-right: 8rem;
}