.photos_content{
      width: 100%;
      height: 42.5rem;
      padding-right: 1rem; 
      overflow-y: auto;
}

.photos_list{
      width: 100%;
      height: auto;
}

.photo_row{
      width: 100%;
      height: fit-content;
      position: relative;
      border-radius: 1rem;
      overflow: hidden;
      margin-bottom: 2rem;
}

.photo_row:before{
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,.6);
      top: 0;
      left: 0;
      opacity: 0;
      transition: .3s ease-in-out;
}

.photo_row:hover:before{
      opacity: 1;
}

.btn_open__modal{
      position: absolute;
      width: 4rem;
      height: 4rem;
      top: calc(50% - 2rem);
      left: calc(50% - 2rem);
      z-index: 2;
      background: var(--primary);
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--text-100);
      transform: scale(0);
      transition: .2s ease-in-out;
      border-radius: .3rem;
}

.btn_open__modal *{
      font-size: 2rem;
}

.photo_row:hover .btn_open__modal{
      transform: scale(1);
}