@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500&family=Rubik:wght@300;400;500;600;700;800&family=Saira+Condensed:wght@200;300;400;500;600&display=swap');

:root{
    /** fonts */
    --ft-1: 'Rubik', sans-serif;
    --ft-2: 'Saira Condensed', sans-serif;
    --ft-3: 'Montserrat', sans-serif;

    /** theme */
    --primary: #c40002;
    /*--secundary: #24d1e1;*/
    --secundary: #fd4e5e;
    --v-gradient: linear-gradient(90deg, transparent, #ffffff, transparent);
    --h-gradient: linear-gradient(transparent, #ffffff, transparent);

    /*** cherry **/
    /*--primary: #7f00ff;
    --secundary: #bb00ff;
    */
    
    /** colors */
    --bg-200: #0d0d0d;
    --bg-100: #343633;
    --text-300: #515151;
    --text-200: #747987;
    --text-100: #fff;
    --border: #373b4c;
    --light: #ffffff;
    --green: #74e25b;
    --red: #d90000;

    /** social color*/
    --facebook: #3b5995;
    --twitter: #171717;
    --google: #dd4b39;
    --youtube: #c4302b;
    --vimeo: #1ab7ea;
    --dribbble: #ea4c89;
    --github: #333;
    --instagram: #c13584;
    --pinterest: #e60023;
    --flickr: #0063dc;
    --bitbucket: #205081;
    --linkedin: #0e76a8;
    --whatsapp: #12af0a;
    --tumblr: #35465c;
    --tiktok: #222;
    --discord: #5865f2;
    --twitch: #9146ff;
    
    /** globals */
    font-family: var(--ft-1);
    font-size: 62.5%;

}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: auto;
    min-height: 100vh;
    overflow-x: hidden;
    background: var(--bg-200);
    color: var(--text-300);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body *{
    font-size: 1.6rem;
    font-family: var(--ft-1);
}

:before,
:after{
    content: '';
    position: absolute;
}

a, span{
    display: inline-block;
}

a{
    text-decoration: none;
}

img{
    widtH: 100%;
    object-fit: cover;
}

ul, ol{
    list-style: none;
}

h1, h2, h3, h4{
    line-height: 120%;
}

button{
    border: none;
    outline: none;
    cursor: pointer;
}

.flex{
    display: flex;
}

.grid{
    display: grid;
}

.col-2{
    grid-template-columns: repeat(2, 1fr);
}

.col-3{
    grid-template-columns: repeat(3, 1fr);
}

.col-4{
    grid-template-columns: repeat(4, 1fr);
}

.container{
    width: 96%;
    max-width: 114rem;
    height: auto;
    position: relative;
    gap: 2.5rem;
    margin: 0 auto;
}

.scroll-y{
    width: 100%;
    height: 42.5rem;
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--bg-100), var(--primary);
}

.scroll-y::-webkit-scrollbar{
    width: .8rem;
    background: var(--bg-100);
    border-radius: .8rem;
}

.scroll-y::-webkit-scrollbar-thumb{
    background: var(--primary);
    border-radius: .8rem;
}

::-webkit-scrollbar{
    width: .8rem;
    background: var(--bg-100);
    border-radius: .8rem;
}

::-webkit-scrollbar-thumb{
    background: var(--primary);
    border-radius: .8rem;
}