.content{
      width: calc(100% - 35rem);
      height: auto;
      position: relative;
      transform: translateX(35rem);
      padding-top: 13.2rem;
      color: var(--text-100);
      padding-left: 2rem;
}

.section_content{
      width: 100%;
      height: auto;
      gap: 3rem;
      margin-top: 4.2rem;
}

.pages_content{
      width: 62rem;
      height: 58.2rem;
      position: relative;
      padding: 1.2rem 0;
}

.pages_content:before,
.pages_content:after{
      width: 37rem;
      height: .2rem;
      background: var(--v-gradient);
}

.pages_content:before{
      top: 0;
      left: 0;
}

.pages_content:after{
      bottom: 0;
      right: 0;
}

.footer_content{
      margin-top: 5rem;
      margin-left: 40rem;
}

.footer_group{
      align-items: center;
      gap: 1.5rem;
      position:relative;
}

.footer_group:before{
      width: 17.5rem;
      height: 2.2rem;
      background: var(--light);
      z-index: -1;
      border-radius: 999px;
}

.footer_logo{
      width: 3.6rem;
      height: 3.6rem;
      background: var(--light);
      align-items: center;
      justify-content: center;
      color: var(--primary);
      font-weight: 800;
      font-size: 2.6rem;
      border-radius: 50%;
      box-shadow: 0 0 5px rgba(0,0,0,.2), 0 0 0 .7rem rgba(255,255,255,.3);
}

.footer_info{
      padding-top: 2.75rem;
}

.footer_info a{
      color: var(--text-200);
      padding-left: 1rem;
}

.footer_info p{
      color: var(--text-200);
      margin-top: 1rem;
}