.online_user{
      position: absolute;
      gap: 1rem;
      top: 4rem;
      right: .75rem;
      transform: translateX(100%);
      align-items: center;
}

.online_dots{
      width: 1.2rem;
      height: 1.2rem;
      border-radius: 50%;
}

.online_dots.green{
      background: var(--green);
}

.online_dots.red{
      background: var(--red);
}

.online_user span{
      text-transform: uppercase;
      font-size: 1rem;
      color: var(--text-100);
}